*, :before, :after {
  box-sizing: inherit;
}

body {
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
}

.main {
  background: radial-gradient(circle, #3f5efb 0%, #fc466b 100%);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: #000;
  text-decoration: none;
}

.container-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button-color_switch {
  -webkit-appearance: none;
  appearance: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  height: 48px;
  text-align: left;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
  background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
  border: 0;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  font-family: JetBrains Mono, monospace;
  font-size: 18px;
  line-height: 1;
  text-decoration: none;
  list-style: none;
  transition: box-shadow .15s, transform .15s;
  display: inline-flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px #2d234266, 0 7px 13px -3px #2d23424d, inset 0 -3px #3a416f80;
}

.button-color_switch:focus {
  box-shadow: inset 0 0 0 1.5px #3c4fe0, 0 2px 4px #2d234266, 0 7px 13px -3px #2d23424d, inset 0 -3px #3c4fe0;
}

.button-color_switch:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #2d234266, 0 7px 13px -3px #2d23424d, inset 0 -3px #3c4fe0;
}

.button-color_switch:active {
  transform: translateY(2px);
  box-shadow: inset 0 3px 7px #3c4fe0;
}

.button-color_switch.disabled {
  background-image: radial-gradient(100% 100% at 100% 0, silver 0 100%);
}

.field {
  display: flex;
}

